.logout-btn {
  cursor: pointer;
  padding: 5.5px;
  margin: 13px 13px 13px 0;
  align-self: flex-end;
  border: 2px solid #707070;
  border-radius: 5px;
  background-color: #cecccc;
  color: #707070;
  font-family: 'Lato', Arial, Helvetica, sans-serif;
  font-size: 18px;
}

.logout-btn:hover {
  background-color: #f7f2f2;
}

.select {
  margin-bottom: 20px;
  width: 75%;
  padding: 6px 10px 9px 10px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 400;
  color: #707070;
  background-color: #f7f2f2;
  border: 1px solid #a09c9c;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: 90%;
}

.input {
  margin-bottom: 22px;
  padding: 8px;
  width: 80%;
  font-size: 19px;
}

.submit-btn {
  cursor: pointer;
  padding: 5px 12px;
  border: 1.8px solid #97bf0f;
  border-radius: 5px;
  font-size: 20px;
  font-weight: 700;
}

.session-option {
  margin-top: 40px;
}

.session-option .session-link {
  font-family: 'Lato', Arial, Helvetica, sans-serif;
  font-size: 25px;
  color: #97bf0f;
}

.loading {
  align-items: center;
  justify-content: center;
}

.loading .loading-img {
  margin-top: 55px;
  height: 200px;
  width: 200px;
  animation: spin 2s infinite linear;
}

.show {
  animation: show 0.5s linear;
}

@keyframes show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

